/* Login.css */

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login_logo {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.login_logoImage {
  font-size: 48px;
  margin-right: 10px;
}

.login_logoTitle {
  font-size: 24px;
}

.login_container {
  width: 300px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
}

.login_container h1 {
  margin-bottom: 20px;
  text-align: center;
}

.login_container form {
  display: flex;
  flex-direction: column;
}

.login_container h4 {
  margin-bottom: 8px;
}

.login_container input {
  margin-bottom: 16px;
  padding: 8px;
  border-radius: 4px;
}

.login_signInButton {
  padding: 8px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login_signInButton:hover {
  background-color: #0056b3;
}

.login_registerButton {
  padding: 8px;
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login_registerButton:hover {
  background-color: #1f8527;
}
